* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Oswald", sans-serif; }

.listStyleNone {
  list-style: none; }

.textDecorationNone {
  text-decoration: none; }

.encabezado {
  color: #3873f3;
  font-size: 33px; }
  .encabezado::before {
    content: "-"; }

.barraCarga {
  height: 53px;
  position: fixed;
  background-color: #3873f3;
  top: 0;
  left: 0;
  z-index: 10; }

.contenedor, .contenedor--secundario {
  display: block;
  padding: 45px 80px;
  min-height: 675px;
  max-width: 100%;
  height: auto; }

.contenedor--secundario {
  background-color: #fcfcfc; }

.diplayHidden {
  display: none; }

span {
  font-weight: bold; }

.btn {
  color: white;
  background-color: #3873f3;
  font-size: 18px;
  padding: 10px 40px;
  border-radius: 8px;
  transition: all 0.3s;
  margin: 15px 0;
  outline: none;
  border: none;
  box-sizing: content-box; }
  .btn:hover {
    background-color: white;
    cursor: pointer;
    border-radius: 0;
    color: #3873f3;
    border-bottom: 2px solid #3873f3; }

.btn {
  margin: 0 20px; }
  .btn-filtro {
    background-color: #fcfcfc;
    color: #3873f3;
    border-radius: 0;
    border-bottom: 2px solid #3873f3; }
    .btn-filtro:hover {
      background-color: #fcfcfc;
      color: #3873f3;
      border-bottom: 2px solid #3873f3; }

.navbar {
  height: 50px;
  width: 100vw;
  display: flex;
  background-color: white;
  position: fixed;
  transition: all 0.8s;
  top: 0;
  left: 0;
  z-index: 100;
  padding: 0 2em;
  align-items: center;
  justify-content: space-between; }
  .navbar__logo {
    display: flex;
    align-items: center;
    letter-spacing: 2px;
    font-weight: 400;
    font-size: 23px;
    color: #0f2432; }
  .navbar__lista {
    display: flex;
    flex-direction: row; }
    .navbar__lista .ul--li {
      margin: 0 30px;
      list-style: none; }
      .navbar__lista .ul--li:hover {
        border-bottom: 2px solid #3873f3;
        padding-bottom: 2px; }
      .navbar__lista .ul--li a {
        text-decoration: none;
        color: #0f2432;
        font-size: 18px;
        font-weight: 400; }
    .navbar__lista-responsivo {
      background-color: #0f2432;
      position: absolute;
      display: flex;
      flex-direction: column;
      align-content: center;
      justify-content: center;
      right: -100%;
      top: 50px;
      padding: 50px;
      width: 100%;
      height: 100vh;
      transition: all 0.8s; }
      .navbar__lista-responsivo .ul--li {
        margin: 0 30px;
        list-style: none;
        padding: 20px 0; }
        .navbar__lista-responsivo .ul--li:hover {
          padding: 20px 0; }
        .navbar__lista-responsivo .ul--li a {
          text-decoration: none;
          color: white;
          font-size: 18px;
          font-weight: 400; }
          .navbar__lista-responsivo .ul--li a:hover {
            border-bottom: 2px solid #3873f3; }
  .navbar svg:hover {
    cursor: pointer; }
  .navbar--oscuro {
    background-color: #0f2432; }
    .navbar--oscuro .ul--li a {
      color: white; }
    .navbar--oscuro .navbar__logo {
      color: white; }
    .navbar--oscuro svg {
      color: white; }
      .navbar--oscuro svg:hover {
        cursor: pointer; }

.active a {
  color: #3873f3; }

.home, .habilidades {
  width: 100%;
  display: flex;
  margin-top: 50px; }
  .home__detalles {
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .home__descripcion {
    font-size: 48px; }
    .home__descripcion h2 {
      margin: 25px 0;
      color: #0f2432; }
      .home__descripcion h2 span {
        color: #3873f3; }
  .home__icons {
    width: 100%;
    height: 50px;
    margin-top: 25px;
    display: flex;
    justify-content: center; }
    .home__icons svg {
      font-size: 25px;
      color: #0f2432;
      margin: 0 30px;
      transition: color 0.3s; }
      .home__icons svg:hover {
        color: #3873f3; }
  .home__img {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center; }
    .home__img img {
      width: 500px;
      padding-left: 30px; }

.sobreMi, .portafolio {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 60px; }
  .sobreMi__descripcion {
    width: 100%;
    margin-top: 50px;
    display: flex;
    flex-direction: row; }
    .sobreMi__descripcion-img {
      width: 40%;
      display: flex;
      justify-content: center;
      margin-right: 20px; }
      .sobreMi__descripcion-img img {
        width: 300px;
        object-fit: cover;
        display: block; }
    .sobreMi__descripcion-parrafo {
      width: 60%;
      display: flex;
      flex-direction: column;
      justify-content: center; }
      .sobreMi__descripcion-parrafo p {
        font-weight: 200;
        color: #0f2432;
        font-size: 20px;
        line-height: 2;
        text-align: justify; }

.habilidades {
  flex-direction: column;
  margin-top: 40px;
  justify-content: space-around; }
  .habilidades__cards {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 60px; }
  .habilidades__card {
    border: 1px solid #999999;
    box-shadow: 0px 0px 15px #999999;
    min-width: 270px;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 8px;
    padding: 3em 2em;
    margin-top: 10px; }
    .habilidades__card-titulo {
      font-size: 38px;
      color: #0f2432;
      margin-bottom: 50px; }
    .habilidades__card-lista {
      display: flex;
      margin-top: 15px;
      width: 100%; }
    .habilidades__card-icon {
      color: #3873f3;
      font-size: 18px;
      min-width: 16px;
      margin: 0 auto; }
    .habilidades__card-descripcion {
      font-weight: 200;
      font-size: 18px;
      margin-right: 10px;
      margin-left: 10px;
      min-width: 55px;
      width: 60px;
      display: block; }
    .habilidades__card-porcentaje {
      min-width: 120px;
      height: 5px;
      border: 1px solid #0f2432;
      margin-top: 8px; }
      .habilidades__card-porcentaje .porcenjate {
        height: 100%;
        background-color: #0f2432;
        display: block; }

.portafolio__filtros {
  display: flex;
  margin-top: 75px;
  justify-content: center; }

.portafolio__cards {
  display: grid;
  gap: 1em;
  grid-template-columns: repeat(auto-fill, minmax(270px, 270px));
  justify-content: space-around;
  align-content: center;
  margin-top: 55px; }

.portafolio__card {
  display: flex;
  flex-direction: column;
  min-height: 350px;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #999999;
  box-shadow: 0px 0px 5px #999999; }

.portafolio__img {
  width: 100%;
  min-height: 130px;
  max-height: 150px; }
  .portafolio__img img {
    width: 100%;
    height: 150px;
    border-radius: 5px;
    transition: all 0.3s; }
    .portafolio__img img:hover {
      transform: scale(1.03); }

.portafolio__descripcion h2 {
  margin: 15px 0; }

.portafolio__descripcion p {
  color: #0f2432;
  font-weight: 200;
  text-align: justify;
  line-height: 1.6;
  margin-bottom: 62px; }

.portafolio__descripcion a {
  display: grid;
  justify-items: center;
  text-decoration: none;
  color: white;
  border-radius: 3px;
  margin-top: 15px;
  padding: 7px 0;
  background-color: #0f2432;
  position: absolute;
  width: 93%;
  bottom: 10px; }
  .portafolio__descripcion a:hover {
    background-color: #3873f3; }

.contactos {
  min-height: 100px;
  display: flex;
  background-color: #0f2432;
  padding: 0 2em;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 2em; }
  .contactos__icons {
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap; }
  .contactos svg {
    color: white;
    margin: 0 45px;
    font-size: 25px;
    margin-top: 5px; }
    .contactos svg:hover {
      color: #3873f3; }
  .contactos__derechos {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 15px; }
    .contactos__derechos > h6 {
      color: white;
      font-size: 15px;
      letter-spacing: 1px;
      font-weight: 200; }

@media screen and (min-width: 860px) {
  .btnX,
  .btnHamburguesa {
    display: none !important; } }

@media screen and (max-width: 1070px) {
  .home__img img {
    padding: 0; } }

@media screen and (max-width: 960px) {
  .home__descripcion {
    font-size: 38px; }
  .sobreMi__descripcion-img img {
    width: 250px; } }

@media screen and (max-width: 860px) {
  .contenedor, .contenedor--secundario,
  .contenedor--secundario {
    padding: 45px 20px; }
  .navbar__lista {
    display: none; }
  .diplayHidden {
    display: flex; }
  .btnX {
    display: none; } }

@media screen and (max-width: 860px) {
  .sobreMi__descripcion-parrafo {
    margin-left: 15px; }
    .sobreMi__descripcion-parrafo p {
      font-size: 18px; } }

@media screen and (max-width: 540px) {
  .sobreMi__descripcion {
    flex-direction: column; }
  .sobreMi__descripcion-img,
  .sobreMi__descripcion-parrafo {
    width: 100%; }
  .sobreMi__descripcion-parrafo {
    margin: 25px 0 0 0; }
  .portafolio__filtros {
    align-content: space-around;
    justify-content: space-around; }
    .portafolio__filtros button {
      padding: 10px 20px; }
  .contactos svg {
    margin-top: 10px; } }

@media screen and (max-width: 430px) {
  .home, .habilidades {
    flex-direction: column; }
  .home__detalles,
  .home__img {
    width: 100%; }
  .home__img {
    margin-top: 60px; }
  .portafolio__filtros button {
    padding: 10px 20px;
    margin: 10px 0; } }

@media screen and (max-width: 370px) {
  .portafolio__filtros {
    flex-wrap: wrap; }
    .portafolio__filtros button {
      padding: 5px 15px;
      margin: 10px 0; }
  .contactos svg {
    margin: 5px 40px; }
  .home__descripcion {
    font-size: 28px; } }
